import React from "react";

export type PageProps = {
    children?: any;
    hasNavBar?: boolean;
};

function ViewWithoutNavbar({ children }: any) {
    const winHeight = window.screen.height;
    return (
        <div style={{ width: '100%', height: winHeight - 45 }}>
            {children}
        </div>
    );
}

export function PageScreen(
    {
        hasNavBar = false,
        children
    }: PageProps
) {

    return (
        <div
            style={{
                position: "fixed",
                height: "100%",
                width: "100%"
            }}
            className="page-screen"
        >
            {
                hasNavBar
                    ? <ViewWithoutNavbar>{children}</ViewWithoutNavbar>
                    : children
            }
        </div>
    );
}
